div.ui.container.main {
  padding-top: 1em;
}

.json-property {
  margin-bottom: 0px;
}

textarea {
  resize: none !important;
  overflow-y: auto;
}

.json-container {
  display: block;
}

.json-container .indent {
  padding-left: 15px;
}

.indent .json-property {
  margin: 0px;
}

.indent .array-value {
  margin: 0px;
}

.keep-text-left {
  text-align: left;
  padding-left: 2%;
}

.collapsible-icon {
  cursor: pointer;
}

.json-tree-container {
  margin: 0em;
  border: 1px solid grey;
  outline: none;
  box-shadow: 0em 0em 0em 0em transparent inset;
  height: 100%;
  width: 100%;
  border-radius: 0.28571429rem;
  line-height: 1.2857;
  padding-top: 7px;
  font-size: 1em;
  font-family: sans-serif;
  overflow-y: auto;
}

.main {
  width: 75% !important;
}
